import mSearch from '../../components/mehaotian-search/mehaotian-search.vue';
export default {
	components: {
		mSearch
	},
	data() {
		return {
			movieList: [],
			keywords: '',
			page: 1,
			pageSize: 12
		};
	},
	// 上拉刷新
	onReachBottom() {
		uni.showLoading({
			title: '加载中...'
		});
		this.page++;
		uni.request({
			url: `${this.$api}/search/list`,
			method: 'POST',
			data: {
				qq: '7929290',
				keywords: this.keywords,
				page: this.page,
				pageSize: this.pageSize
			},
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			success: res => {
				if (res) {
					uni.hideLoading();
					res.data.data.rows.map(item => {
						this.movieList.push(item);
					});
				}
			},
			fail: err => {
				console.log(err);
			}
		});
	},
	methods: {
		getList() {
			uni.showLoading({
				title: '加载中...'
			});
			uni.request({
				url: `${this.$api}/search/list`,
				method: 'POST',
				data: {
					qq: '7929290',
					keywords: this.keywords,
					page: this.page,
					pageSize: this.pageSize
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: res => {
					if (res) {
						uni.hideLoading();
						this.movieList = res.data.data.rows;
					}
				},
				fail: err => {
					console.log(err);
				}
			});
		},
		search(e) {
			this.keywords = e;
			this.page = 1;
			this.pageSize = 12;
			this.getList();
		},
		goDetail(item, index) {
			uni.navigateTo({
				url: '/pages/detail/detail?item=' + JSON.stringify(item)
			});
		}
	},
	onLoad() {
		this.getList();
	}
};
